export class HomeScreen extends React.Component {
  
  constructor(props){
    super(props)
    this.state = {
      notes: [],
      noteCategoriesVisible: false
    }
  }

  refreshLastOpened = () =>
  {
    let patientService = new PatientService()
    this.setState({notes : []}, () => {
      patientService.getLastOpenedNotes().then((innerNotes) => {       
        this.setState({notes: []})

        let promises = []

        innerNotes.forEach(innerNote => {
          promises.push(patientService.getPatientById(innerNote.patient))
        })

        Promise.all(promises).then(val => {
          this.valKeys = {}
          console.log(val)
          val.forEach(item => {
            if(item[0] !== undefined)
              this.valKeys[item[0].id] = item[0].name
          }, this)

          let notes = innerNotes.map(innerNote => {
            innerNote["patientName"] = this.valKeys[innerNote.patient]
            return innerNote
          })

          this.setState({notes: notes})
        })


      })
    })
  }
  
  componentDidMount() {
    this.listener = this.props.navigation.addListener("didFocus", this.refreshLastOpened);
    this.refreshLastOpened()

  }

  setNoteCategoriesVisible(state) {
    this.setState({noteCategoriesVisible: state});
  }

  reset() {
    //AsyncStorage.getAllKeys().then(AsyncStorage.multiRemove)
    AsyncStorage.setItem('notes', JSON.stringify(null), () => {
      this.refreshLastOpened()
    });

    AsyncStorage.setItem('patients', JSON.stringify(null), () => {
      this.refreshLastOpened()
    });
  }
  
  render() {
    return (
      <View style={styles.mainContainer}>
        <ImageBackground source={images['background'].img} style={styles.image}>
        <StatusBar barStyle="light-content" />
        <KeyboardAvoidingView style={{ flex: 1, alignItems: 'center', justifyContent: 'center'}}>
          <View style={{height: 1, width: 1, marginBottom: 10}}>
          </View>
          <View>
            {    
              Platform.OS !== 'web'?        
            <StyledButton
              icon={{name:'note-add', color:'#ffffff'}}
              backgroundColor='#03A9F4'
              buttonStyle={{borderRadius: 10, marginLeft: 0, marginRight: 0, marginBottom: 10, width: 400}}
              onPress={() => new Alerts().noteAlert(this.props.navigation)}
              title="Create a New Note"
              />
              :
            <StyledButton
              icon={{name:'note-add', color:'#ffffff'}}
              backgroundColor='#03A9F4'
              buttonStyle={{borderRadius: 10, marginLeft: 0, marginRight: 0, marginBottom: 10, width: 400}}
              onPress={() => this.setNoteCategoriesVisible(true)}
              title="Create a New Note"
              />
              
            }
            <WebAlert navigation={this.props.navigation} visible={this.state.noteCategoriesVisible} callback={(state) => this.setNoteCategoriesVisible(state)}/>

            <StyledButton
              icon={{name:'account-circle', color:'#ffffff'}}
              backgroundColor='#03A9F4'
              buttonStyle={{borderRadius: 10, marginLeft: 0, marginRight: 0, marginBottom: 10}}
              onPress={() => this.props.navigation.navigate('Patients')}
              title="View and add Patients"
            />
            <StyledButton
              icon={{name:'format-quote', color:'#ffffff'}}
              backgroundColor='#03A9F4'
              buttonStyle={{borderRadius: 10, marginLeft: 0, marginRight: 0, marginBottom: 0}}
              onPress={() => this.props.navigation.navigate('Categories')}
              title="Categories and Phrases"
            />

            <View style={{flexDirection: 'column', justifyContent:'center', borderRadius: 10, alignItems: "center"}}>              

              <Text style={{color: '#ffffff', marginTop: 20, justifyContent:'center'}}>Last Opened Notes</Text>
              {
                this.state.notes.filter(item => {
                    return item.text !== undefined
                }).map((item) => {
                    let txt = item.text.replace(/(\r\n|\n|\r)/gm," ").slice(0, 30)
                    txt += item.text.length > 30? '...' : '';
                  return(
                    Platform.OS === 'ios'? <Button title={txt} key={item.id} onPress={() => {this.props.navigation.navigate('NoteDetail', {"action": "edit", "type": item.type, "patient": item.patient, "noteId": item.id, "text": item.text, "patientName": item.patientName})}}/> :
                    Platform.OS === 'web'? <StyledButton buttonStyle={styles.webButton} title={txt} key={item.id} onPress={() => {this.props.navigation.navigate('NoteDetail', {"action": "edit", "type": item.type, "patient": item.patient, "noteId": item.id, "text": item.text, "patientName": item.patientName})}}/> :null  
                  )
                })
              }
              
              <View>{
                //<Button title="Reset Notes" key={"reset"} onPress={() => this.reset()}/>
              }
              </View>
        </View>

          </View>

        </KeyboardAvoidingView>
        </ImageBackground>
      </View>
    );
  }  
}




  export class NoteDetailScreen extends React.Component {
    constructor(props){
      super(props)

      console.log("props-------------------------")
      console.log(props.navigation.state.params)
      console.log("------------------")
      
      const action = props.navigation.getParam("action", "")
      const type = props.navigation.getParam("type", "")
      var patientText = props.navigation.getParam("patientName", "")
      var noteText = props.navigation.getParam("text", "")

      var noteId = ""
      if(action == "new" || action == "duplicate")
        noteId = Math.random().toString(36).replace(/[^a-z]+/g, '').substr(0, 10);
      if(action == "edit")
      {
        noteId = props.navigation.getParam("noteId", "")
        let patientService = new PatientService();

        patientService.getNote(noteId).then((note) =>{
          patientService.setNoteOpenDate(note)
        })
      }

      this.state = {
        patientText: patientText,
 //       noteText : "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Duis laoreet ipsum id arcu tincidunt luctus. Mauris et lectus sit amet purus posuere pharetra. Duis vulputate rhoncus tortor, vel feugiat nisi tempus at. Pellentesque massa nunc, accumsan at tempor nec, placerat in diam. Nam ultrices molestie faucibus. Nunc ut metus eget erat interdum sodales. Sed diam urna, cursus mollis enim volutpat, commodo imperdiet ligula. Sed sed lobortis lacus. Proin vel leo nisl. Nulla tempor metus non orci imperdiet, et gravida turpis posuere. Cras porta dictum mauris ac egestas. Integer cursus metus id nunc bibendum, non volutpat diam finibus. Donec dapibus dictum ante, in viverra erat semper tempor. Aenean feugiat turpis sed diam convallis imperdiet. Aliquam pretium et tellus vestibulum viverra. Phasellus quis lacinia augue. Donec viverra nec enim sed placerat. Ut tempor interdum magna et varius. Phasellus ut quam eu massa sagittis facilisis. In turpis odio, tempus eget leo eget, volutpat varius massa. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Pellentesque convallis tempor mi. ",
        noteText : noteText,
        noteInputFocus: false,
        showSuggestion : false,
        selection: {
          start: 0,
          end: 0
        },
        action: action,
        type: type,
        noteId: noteId

      }

      this.props.navigation.setParams({
        addTextContent: (text) => this._addTextContent(text)
     });
    }

    handleSelectionChange = ({ nativeEvent: { selection } }) => this.setState({ selection })

    static navigationOptions = ({ navigation }) => {
      const {params} = navigation.state || {};
      
      return {
        headerTitle: (
          <View>
            <Text>Patient Note</Text>  
          </View>
        )
        ,
        headerLeft:(
          <View style={{flex: 1, flexDirection: 'row'}}>
            <View style={{position: 'absolute', top: -50, left: 0, right: 0, bottom: 0, justifyContent: 'center', alignItems: 'center', width: Dimensions.get('window').width}}>
              <FlashMessage position="top" style={{justifyContent: 'center', alignItems: 'center', marginTop: 10}} />
            </View>
            <StyledButton   
              icon={{
                name: 'menu',
                size: 15,
                color: '#007AFF'
              }}
              buttonStyle={{
                backgroundColor: "transparent",
              }}
              color= {"#007AFF"}
              onPress={() => {navigation.toggleDrawer()}} title="Menu" />
              

            <StyledButton   
              buttonStyle={{
                backgroundColor: "transparent",
              }}
              color= {"#007AFF"}
              onPress={() => {
                
                console.log(navigation); 
                //navigation.push('Home')
                console.log(navigation.reset)

                const resetAction = StackActions.reset({
                  index: 0,
                  actions: [NavigationActions.navigate({ routeName: 'Home' })],
                });
                navigation.dispatch(resetAction);

                }} title="Home" />


            <StyledButton   
              buttonStyle={{
                backgroundColor: "transparent",
              }}
              color= {"#007AFF"}
              onPress={() => {navigation.goBack()}} title="Back" />

          </View>
        ),
        headerRight: (
          <View style={{flex: 1, flexDirection: 'row'}}>
            <Button onPress={()=>{navigation.state.routes[0].params.saveContent()}} title="Save" />
          </View>
        ),
      };
    };

    _addTextContent(text)
    {
      console.log(text)

      let result =  this.state.noteText.slice(0, this.state.selection.start) 
      + text 
      + this.state.noteText.slice(this.state.selection.start, this.state.noteText.length)

      console.log(result)

      this.setState({'noteText': result})
    }

    componentDidMount () {
      this.keyboardDidShowListener = Keyboard.addListener('keyboardWillShow', () => this._keyboardDidShow());
      this.keyboardDidHideListener = Keyboard.addListener('keyboardWillHide', () => this._keyboardDidHide());
      //this.props.navigation.toggleDrawer()
      this.props.navigation.setParams({ saveContent: () => this.saveNote() });
    }

  
    componentWillUnmount () {
      this.keyboardDidShowListener.remove();
      this.keyboardDidHideListener.remove();
    }
  
    _keyboardDidShow () {
      this.setState({showSuggestion: true})
    }
  
    _keyboardDidHide () {
      this.setState({showSuggestion: false})
    }

    saveNote()
    {
      try{
        let patientService = new PatientService();
        let patient = new Patient();
        let note = new Note();

        note.setId(this.state.noteId)
        note.setType(this.state.type)
        note.setText(this.state.noteText)
        patient.setPatientName(this.state.patientText);

        console.log(note)
        console.log(patient)

        patientService.savePatientNote(patient, note)

        showMessage({
          message: "Note Saved",
          type: "info",
          style: {paddingTop: 100},
        });
      }
      catch(e){
        console.log(e)
        if(e == 'PatientNameEmpty') {
          showMessage({
            message: "Cannot save note - patient name is empty",
            type: "danger",
            style: {paddingTop: 100},
          });
        }
    }

    }

    openNote = (note) =>
    {
      console.log(note)
      let patientService = new PatientService();

      patientService.getNote(note.id).then((innerNote) =>{
        patientService.setNoteOpenDate(note)

        
        patientService.getPatientById(innerNote[0].patient).then((p) => {
          this.setState({"action": "edit"})
          this.setState({"type": note.type})
          this.setState({"noteId": note.id})
          this.setState({"noteText": note.text})
          this.setState({patientText: p[0].name})


          
        })
      })
        

      
      
    }
    

    render() {
      return (
        <View style={styles.container}>
            
            <StatusBar barStyle="light-content" />
            <TabComponent noteHandler={this.openNote}/>
            <ScrollView>
              
              <TextInput
                style={styles.patientText}
                onChangeText={(patientText) => this.setState({patientText})}
                value={this.state.patientText}
                onFocus={() => this.setState({noteInputFocus: false})}
                placeholder="Patient Name"
                editable={this.state.action != 'edit'}
              />        
              <TextInput
                onChangeText={(noteText) => this.setState({noteText})}
                value={this.state.noteText}
                onSelectionChange={this.handleSelectionChange} 
                onFocus={() => this.setState({noteInputFocus: true})}
                style={styles.noteText}
                multiline={true}
                placeholder="Note Text"
                ref="noteText"
              />

              </ScrollView>

              {Platform.OS === 'web'? <WebSuggestionComponent  next={()=> {return this.refs.noteText}} handler={(text) => this._addTextContent(text)} /> : null }         
              
              <KeyboardAvoidingView
                    behavior='padding'
                    style={{ backgroundColor: '#eeeeee' }}
              >
                { this.state.showSuggestion && this.state.noteInputFocus ? <SuggestionComponent  next={()=> {return this.refs.noteText}} handler={(text) => this._addTextContent(text)} /> : null }                
              </KeyboardAvoidingView>
          
        </View>
      );
    }  
  }

  class TabComponent extends React.Component {
    constructor(props){
      super(props)
      this.state = {
        data: []
      }
      this.refreshTabs()

    }

    refreshTabs(){
      let patientService = new PatientService()
      patientService.getLastOpenedNotes().then((notes) => {
        this.setState({data: notes})
      })
    }

    tabPress(item)
    {
      this.props.noteHandler(item)
      this.refreshTabs()
    }

    render() {
      return (
        <View style={{flexDirection: 'row', justifyContent:'center', borderRadius: 10, backgroundColor: "#eeeeee"}}>
          {
            this.state.data.filter(item => {
              return item.text !== undefined
            }).map((item) => {
                let txt = item.text.slice(0, 10) + "..."
              return <Button title={txt.replace(/\n/g, ' ') + "..."} key={item.id} onPress={() => {this.tabPress(item)}}/>    
            })
          }
        </View>
      )
    }
  }

  class SuggestionComponent extends React.Component {    
    constructor(props){
      super(props)
      
      this.p = new PatientService()
      this.m = new MenuService()
      
      this.state = {
        patientText: "Patient Name",
        suggestionText: "",
        data: [],
        searchMode: false
      }

    }

    searchText(suggestionText){
      this.setState({suggestionText})

      if(suggestionText.startsWith(".")){
        this.m.searchPhrasesByShortcut(suggestionText.substring(1)).then((val) => {
          let data = val.map(val2 => {
            return {"key": Math.random().toString(36).replace(/[^a-z]+/g, '').substr(0, 25), "val": {'text': val2.slice(0,200)}}
          }).slice(0,5)
          this.setState({data: data})       
        })
      }
      else{
        this.p.getNotesByPhrase(suggestionText).then((val) => {
          let data = val.map(val2 => {
            return {"key": Math.random().toString(36).replace(/[^a-z]+/g, '').substr(0, 25), "val": {'text': val2.slice(0,200)}}
          }).slice(0,5)
          this.setState({data: data})
        })
  
        this.m.searchPhrases(suggestionText).then((val) => {
          let data = val.map(val2 => {
            return {"key": Math.random().toString(36).replace(/[^a-z]+/g, '').substr(0, 25), "val": {'text': val2.slice(0,200)}}
          }).slice(0,5)
          this.setState({data: this.state.data.concat(data)})       
        })
      }

    }

    toggleSearchmode(){
      if(!this.state.searchMode)
        this.refs.inputSuggestion.focus()
      else{
        console.log(this.props)
        this.props.next().focus()
      }
      this.setState({searchMode: !this.state.searchMode})
    }
    
    listItemPress(text){
      console.log("inserting" + text)
      this.props.handler(text + ". ")
    }

    render(){
      return (<View style={{marginBottom: 70}}>
        { 
          this.state["data"].map(item => {
            return <Button key={item.key} title={item.val.text} onPress={() => {this.listItemPress(item.val.text)}}/>
          })
        }

        <Button title="Search Mode" onPress={()=> {this.toggleSearchmode()}}/>
        <ScrollView  keyboardShouldPersistTaps={'always'}>
        <TextInput
          onChangeText={(suggestionText) => this.searchText(suggestionText)}
          blurOnSubmit={false} 
          onBlur={() => this.props.next().focus()}
          value={this.state.suggestionText}
          ref="inputSuggestion"
        >
        </TextInput>
        </ScrollView>
      </View>)
    }
  }

  class WebSuggestionComponent extends SuggestionComponent {
    constructor(props){
      super(props)
    }

    focus(){
      this.props.next().focus()
      this.searchText('')
    }

    render(){
      return (<View style={{marginBottom: 10, marginLeft: 20, marginRight: 20}}>
        { 
          this.state["data"].map(item => {
            return <Button key={item.key} title={item.val.text} onPress={() => {this.listItemPress(item.val.text)}}/>
          })
        }

        <ScrollView>
          <TextInput
            onChangeText={(suggestionText) => this.searchText(suggestionText)}
            blurOnSubmit={false} 
            onBlur={() => this.focus()}
            value={this.state.suggestionText}
            ref="inputSuggestion"
            placeholder="Enter text for suggestions"
            style={{fontSize:16, borderWidth: 1, borderColor: '#eeeeee'}}
          >
          </TextInput>
        </ScrollView>
      </View>)
    }

  }


export class PatientsScreen extends React.Component {
    constructor(props){
      super(props)
      let patientService = new PatientService();
      this.state = {
        'data': []
      }
      patientService.getAllPatients().then((patients) => {
        let ps = JSON.parse(patients)

        if(patients && ps)
        {
          let data = ps.map((value, index, array) => {return {"key": String(value.id), "val": {'name': String(value.name)}}})
          if(data)
            this.setState({'data': data})
        }
      });
      

    }
    render() {
      return (
        <View  style={styles.container}>
          <StatusBar barStyle="light-content" />
          <ScrollView style={{flex:1}}>
          <FlatList
              data={this.state.data}
              renderItem={({item}) => 
              <ListItem
                key={item.key}
                title={item.val.name}
                leftIcon={{name: 'account-circle'}}
                onPress={() => {this.props.navigation.navigate('PatientDetail', {"key": item.key, "name": item.val.name})}}
            />
            }
            />
          </ScrollView>
        </View>
      );
    }  
  }
  
  export class PatientDetailScreen extends React.Component {
    constructor(props){
      super(props)
      this.state = {
        'data': [],
        'noteCategoriesVisible': false,
        'patientName': ''
      }
      console.log(this.props.navigation.getParam("key", -1))
      //todo: need to add async then calls
      let patientService = new PatientService();
      let key= parseInt(this.props.navigation.getParam("key", -1));
      let name = this.props.navigation.getParam("name", "")

      patientService.getPatientNotesById(key).then((r,e) =>{
        let notes = r
        if(r){
          notes = notes.map((value, index, array) => {
            let d = new Date(0).toString();
            if(value.updateDate !== undefined)
              d = new Date(value.updateDate).toString();

            return {"key": String(index), "val": {'description': String(value.text), 'patient': value.patient, 'type': value.type, "id": value.id, "patientName": name, "date": d}}
          })
          this.setState({'data': notes})
        }
        
      })
    }

    setNoteCategoriesVisible(state) {
      this.setState({noteCategoriesVisible: state});
    }

    render() {
      return (
        <View  style={styles.container}>
          <StatusBar barStyle="light-content" />
          <ScrollView style={{flex:1}}>

          <FlatList
              data={this.state.data}
              renderItem={({item}) => 
              <ListItem
                style={{backgroundColor: '#ffffff'}}
                key={item.key}
                title={item.val.description}
                subtitle={
                  <View style={{flex:1, flexDirection: 'row'}}>
                    <Text style={styles.ratingText} style={{paddingTop: 12, paddingLeft: 10}}>{item.val.type}</Text>
                    <Text style={styles.ratingText} style={{paddingTop: 12, paddingLeft: 10}}>{item.val.date}</Text>
                    <Button title="Duplicate"
                    onPress={() => {this.props.navigation.navigate('NoteDetail', {"action": "duplicate", "type": item.val.type, "patient": item.val.patient, "noteId": item.val.id, "text": item.val.description, "patientName": item.val.patientName})}}
                    />
                    
                  </View>
                }
                
                onPress={() => {this.props.navigation.navigate('NoteDetail', {"action": "edit", "type": item.val.type, "patient": item.val.patient, "noteId": item.val.id, "text": item.val.description, "patientName": item.val.patientName})}}
              />
              }
            />
          <View style={{justifyContent:'center', flex: 1,  alignItems: 'center', marginTop: 10}}>
          {    
            Platform.OS !== 'web'? 
            <StyledButton
                    icon={{name:'note-add', color:'#ffffff'}}
                    backgroundColor='#03A9F4'
                    buttonStyle={{borderRadius: 10, marginLeft: 0, marginRight: 0, marginBottom: 10, width: 400}}
                    title="Create a New Note For This Patient"
                    onPress={() => new Alerts().noteAlert(this.props.navigation, this.props.navigation.getParam("name", ""))} 
                  />
            :
            <StyledButton
                  icon={{name:'note-add', color:'#ffffff'}}
                  backgroundColor='#03A9F4'
                  buttonStyle={{borderRadius: 10, marginLeft: 0, marginRight: 0, marginBottom: 10, width: 400}}
                  title="Create a New Note For This Patient"
                  onPress={() => this.setState({noteCategoriesVisible: true})} 
                />
            }
            <WebAlert navigation={this.props.navigation} visible={this.state.noteCategoriesVisible} callback={(state) => this.setNoteCategoriesVisible(state)} patientName={this.props.navigation.getParam("name", "")}/>

          
          </View>
          </ScrollView>
        </View>
      );
    }  
  }
  class WebAlert extends React.Component  {
    constructor(props){
      super(props)
      this.state = {
        'data': [],
      }
    }

    render()
    {
      return (
        this.props.visible === true? 
        <View>
          <StyledButton buttonStyle={styles.webButton2} title="Consult" onPress={() => this.props.navigation.navigate('NoteDetail', {action: "new", type: "consult", patientName: this.props.patientName})}></StyledButton>
          <StyledButton buttonStyle={styles.webButton2} title="Follow Up" onPress={() => this.props.navigation.navigate('NoteDetail', {action: "new", type: "followup", patientName: this.props.patientName})}></StyledButton>
          <StyledButton buttonStyle={styles.webButton2} title="Other" onPress={() => this.props.navigation.navigate('NoteDetail', {action: "new", type: "other", patientName:this.props.patientName})}></StyledButton>
          <StyledButton buttonStyle={styles.webButton2} title="Cancel" onPress={() => this.props.callback(false)}></StyledButton>
        </View>
        :
        <View></View>
      )
    }
  }


  class Alerts extends React.Component  {
    noteAlert(navigation, patientName){
        Alert.alert(
          'Type of Note',
          'Please select the type of note you would like to create',
          [
            {text: 'Consult', onPress: () => navigation.navigate('NoteDetail', {action: "new", type: "consult", patientName: patientName})},
            {text: 'Follow Up', onPress: () => navigation.navigate('NoteDetail',  {action: "new", type: "followup", patientName: patientName})},
            {text: 'Other', onPress: () => navigation.navigate('NoteDetail', {action: "new", type: "other", patientName: patientName})},
            {text: 'Cancel', onPress: () => console.log('cancel')},
          ],
          { cancelable: true }
        )
    }
  }